import loginStyles from './login.module.css';
import { Input, PasswordInput, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { useState, useRef } from 'react';
import { Link } from "react-router-dom";
import { useDispatch } from 'react-redux';
import { login } from '../../../services/userSlice';
import { useNavigate } from "react-router-dom";
import { useForm } from '../../../hooks/useForm';




function Login() {

    const inputRef = useRef(null);

    const navigate = useNavigate();

    const dispatch = useDispatch()

    const { values, handleChange } = useForm({  email: '', password: '', });

    const onSubmit = (payload) => {
        dispatch(login(payload))
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        onSubmit(values)
        navigate("/", { replace: true });
    }

    return (
        <section className={loginStyles.section}>
            <form className={loginStyles.form} onSubmit={handleSubmit}>
                <h2 className="text text_type_main-medium"></h2>
                <Input
                    type={'email'}
                    placeholder={'E-mail'}
                    onChange={handleChange}
                    value={values.email}
                    name={'email'}
                    error={false}
                    ref={inputRef}
                    errorText={''}
                />
                <PasswordInput
                    onChange={handleChange}
                    value={values.password}
                    name={'password'}
                    placeholder={''}
                />
                <Button htmlType="submit" type="primary" size="large" disabled={!values.email || !values.password}></Button>
            </form>
            <span className="text text_type_main-default text_color_inactive pt-20">
                 -  ? <Link to="/register" className={loginStyles.link}></Link>
            </span>
            <span className="text text_type_main-default text_color_inactive pt-4">
                 ? <Link to="/forgot-password" className={loginStyles.link}> </Link>
            </span>
        </section>
    )
}




export default Login;