import resetPasswordStyles from './reset-password.module.css'
import { Link } from "react-router-dom";
import { useState, useRef } from 'react';
import { Input, PasswordInput, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { resetPassword } from '../../../services/userSlice';
import { useDispatch } from 'react-redux';
import { useNavigate } from "react-router-dom";
import { useForm } from '../../../hooks/useForm';
import { Navigate } from "react-router-dom";



function ResetPassword() {

    const inputRef = useRef(null);

    const navigate = useNavigate();

    const dispatch = useDispatch()

    const { values, handleChange } = useForm({ password: '', token: '' });

    const onSubmit = (payload) => {
        dispatch(resetPassword(payload))
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        onSubmit(values)
        navigate("/login", { replace: true });
    }

    if (!localStorage.getItem('email')) {
        return <Navigate to={'/forgot-password'} replace={true} />;
    }

    return (
            <section className={resetPasswordStyles.section}>
                <form className={resetPasswordStyles.form} onSubmit={handleSubmit}>
                    <h2 className="text text_type_main-medium"> </h2>
                    <PasswordInput
                        onChange={handleChange}
                        value={values.password}
                        name={'password'}
                        placeholder={'  '}
                    />
                    <Input
                        type={'text'}
                        placeholder={'   '}
                        onChange={handleChange}
                        value={values.token}
                        name={'token'}
                        ref={inputRef}
                        errorText={''}
                    />
                    <Button htmlType="submit" type="primary" size="large" disabled={!values.password || !values.token}></Button>
                </form>
                <span className="text text_type_main-default text_color_inactive pt-20">
                     ? <Link to="/login" className={resetPasswordStyles.link}></Link>
                </span>
            </section>
    )
}




export default ResetPassword;