import orderStyles from "./order-details.module.css"
import checkMarkGraphics from "../../images/graphics.svg"
import { useSelector } from 'react-redux';


function OrderDetails() {

    const { orderNumber } = useSelector(
        (store) => store.order);

    return (
        <>
            <ul className={orderStyles.container}>
                <li className={orderStyles.detail}>
                    <p className={`${orderStyles.number} text text_type_digits-large pb-5`}>{orderNumber}</p>
                    <p className="text text_type_main-medium pt-3"> </p>
                </li>
                <li className={orderStyles.detail}>
                    <img src={checkMarkGraphics} alt='  '></img>
                </li>
                <li className={orderStyles.detail}>
                    <p className="text text_type_main-default">   </p>
                    <p className="text text_type_main-default text_color_inactive pt-2">    </p>
                </li>
            </ul>
        </>
    )
}

export default OrderDetails